import React from 'react';
import { StatusBar } from 'expo-status-bar';
import { Text, View, ScrollView, Image, TouchableWithoutFeedback} from 'react-native';
import { styles } from '../styles';

export default function TiKydaIdesh({navigation}) {
  
   return (
     <View style={styles.container}>
 
       <View style={styles.header}>
          <TouchableWithoutFeedback onPress={() => navigation.goBack()}>
            <Image  source={require('./../assets/icons8.png')} style={styles.Arrow} />
          </TouchableWithoutFeedback>
          <Image source={require('./../assets/menu.png')} style={styles.Menu}/>
       </View>

        <ScrollView>
              <Text style={styles.Song} >
                  <Text style={styles.Couplet} > 1: {'\n'}</Text>
                  <Text style={styles.Accordes}> {'\n'}</Text>
                  "  ,  ,{'\n'}
                  <Text style={styles.Accordes}>F         G{'\n'}</Text>
                      ?" -{'\n'}
                  <Text style={styles.Accordes}>F         G{'\n'}</Text>
                  "  {'\n'}
                  <Text style={styles.Accordes}>C         G         C{'\n'}</Text>
                      .{'\n'}
                  
                  <Text style={styles.Accordes}>F         G{'\n'}</Text>
                  //    ,{'\n'}
                  <Text style={styles.Accordes}>C         A{'\n'}</Text>
                     ,{'\n'}
                  <Text style={styles.Accordes}>F         G{'\n'}</Text>
                      {'\n'}
                  <Text style={styles.Accordes}>C         G         C{'\n'}</Text>
                    , ". // 2.{'\n'}{'\n'}

                  <Text style={styles.Couplet} > 2: {'\n'}</Text>
                  ",    {'\n'}
                      ?" -{'\n'}
                  " {'\n'}
                     .{'\n'}
                  //   {'\n'}
                    ,{'\n'}
                     {'\n'}
                    ". // 2.{'\n'}{'\n'}

                  <Text style={styles.Couplet} > 3: {'\n'}</Text>
                  "    {'\n'}
                     ?" - {'\n'}
                  ",   {'\n'}
                    ! {'\n'}
                   //    ,{'\n'}
                      {'\n'}
                   {'\n'}
                  ,   ". // 2.{'\n'}{'\n'}

                  <Text style={styles.Couplet} > 4: {'\n'}</Text>
                  "     ,{'\n'}
                    ". -{'\n'}
                  ",  ,   ,{'\n'}
                     !{'\n'}
                  //   {'\n'}
                    .{'\n'}
                   , {'\n'}
                      . // 2.{'\n'}

              </Text>
        </ScrollView> 

       <StatusBar style="auto" />
     </View>
   );
     
 }
